html, body {
  font-family: 'Open Sans', 'Noto Sans SC', sans-serif;
  font-size: 14px;
  color: #FFFFFF;
  background: #030A1C;
  line-height: 1.5;
  letter-spacing: 2px;
  margin: 0px;
  font-weight: 200; }

* {
  box-sizing: border-box; }

input {
  font-size: initial;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.54);
  padding: 6px 0px 4px 0px;
  color: #BEC6DB; }

input:focus {
  font-size: initial;
  border-bottom: 1px solid #FFFFFF;
  outline: none; }

button {
  background: transparent;
  border: none;
  color: #FFFFFF;
  font-size: 1.125rem; }

select {
  font-size: initial;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.54);
  padding: 6px 0px 0px 0px;
  color: #BEC6DB; }

select:focus {
  font-size: initial;
  border: none;
  outline: none; }

option {
  background: #30394B;
  color: #8B8E97; }

textarea {
  font-size: initial;
  background-color: transparent;
  resize: none; }

textarea:focus {
  font-size: initial;
  outline: none; }

h1 {
  margin: 0px; }

p {
  margin: 6px 0px 6px 0px;
  font-weight: 400; }

a {
  text-decoration: none;
  color: #FFFFFF; }

.fade-enter-active,
.fade-leave-active {
  transition: opacity .2s; }

.fade-enter,
.fade-leave-to {
  opacity: 0; }

.slide-down-enter-active,
.slide-down-leave-active {
  transition: all .3s; }

.slide-down-enter {
  height: auto;
  transform: translateY(-100%); }

.slide-down-leave-to {
  height: 0;
  transform: translateY(-100%); }

.switch-enter-active, .switch-leave-active {
  transition: opacity .5s; }

.switch-enter, .switch-leave-to {
  opacity: 0; }

.chat-box {
  position: relative;
  padding: 12px 16px;
  background-color: #1D2331;
  color: #BEC6DB;
  line-height: 20px;
  border-radius: 6px; }
  .chat-box.system {
    max-width: 220px; }
    .chat-box.system:before {
      display: block;
      content: "";
      position: absolute;
      top: 12px;
      left: -8px;
      width: 0;
      height: 0;
      transform: rotate(-45deg);
      border-width: 10px;
      border-color: #1D2331 transparent transparent #1D2331;
      border-style: solid;
      border-top-left-radius: 4px; }
  .chat-box:not(.system) {
    width: 290px; }
    .chat-box:not(.system):before {
      display: block;
      content: "";
      position: absolute;
      top: 12px;
      right: -8px;
      width: 0;
      height: 0;
      transform: rotate(45deg);
      border-width: 10px;
      border-color: #1D2331 #1D2331 transparent transparent;
      border-style: solid;
      border-top-right-radius: 4px; }

@media (min-width: 1023px) {
  .chat-box.system {
    max-width: 330px; }
  .chat-box:not(.system) {
    width: 330px; } }

.input-block {
  position: relative;
  width: 100%; }
  .input-block:not(:last-child) {
    margin-bottom: 32px; }
  .input-block input {
    width: 100%; }
  .input-block .error-msg {
    position: absolute;
    left: 0;
    top: auto;
    font-size: 12px;
    line-height: 14px;
    color: #EB5757;
    margin-top: 4px; }

.inline-input-block {
  display: flex;
  justify-content: space-between; }
  .inline-input-block .input-block:first-child {
    margin-right: 24px; }

/* Open Sans */
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  src: url(/robotdemo/dist/fonts/OpenSans.629a55a.ttf); }

.echarts {
  width: 600px;
  height: 400px;
}
